	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>More complex artwork and text have made diving into the thrilling tales of our favourite superheroes a challenge for readers with impaired vision. Comicanic updates the popular print standard type used in action comics for improved legibility at small sizes, while still maintaining the fun, dynamic voice we all know and love.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 137.25015142337978%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Examples of the Comicanic typeface in use.\"\n        title=\"\"\n        src=\"/the-next-website/static/lklsdf-01-57204ca36a9edcd025d42a9b7549ad2a-bc86b.png\"\n        srcset=\"/the-next-website/static/lklsdf-01-57204ca36a9edcd025d42a9b7549ad2a-c3a8a.png 125w,\n/the-next-website/static/lklsdf-01-57204ca36a9edcd025d42a9b7549ad2a-54de6.png 250w,\n/the-next-website/static/lklsdf-01-57204ca36a9edcd025d42a9b7549ad2a-bc86b.png 500w,\n/the-next-website/static/lklsdf-01-57204ca36a9edcd025d42a9b7549ad2a-da458.png 750w,\n/the-next-website/static/lklsdf-01-57204ca36a9edcd025d42a9b7549ad2a-b8502.png 1000w,\n/the-next-website/static/lklsdf-01-57204ca36a9edcd025d42a9b7549ad2a-34ab4.png 1500w,\n/the-next-website/static/lklsdf-01-57204ca36a9edcd025d42a9b7549ad2a-86135.png 1651w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>![Letters in Comicanic and speech bubbles showing the type at different sizes](../assets/specimen print to show people2-01.png)</p>\n<p>Features include:</p>\n<ul>\n<li>Improved readability in long-form text and at small point sizes.</li>\n<li>Better legibility in common comic book formats, such as speech bubbles and captions.</li>\n<li>Letters built to support layering, strokes, colour changes and other fun stylistic variants. </li>\n</ul>\n<p>![Images describing the different features of the typeface Comicanic](../assets/specimen print to show people-07.png)</p>\n<p>![Comic demonstrating Comicanic in paragraph form](../assets/specimen print to show people2-02.png)</p>","frontmatter":{"title":"Comicanic","graduate":"Jayme Kalpin","category":"Typography","category2":null,"category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.5,"src":"/the-next-website/static/comicanic-01-4225c2494c6188c5c17317b792cc36f5-0f681.png","srcSet":"/the-next-website/static/comicanic-01-4225c2494c6188c5c17317b792cc36f5-c1674.png 375w,\n/the-next-website/static/comicanic-01-4225c2494c6188c5c17317b792cc36f5-34f47.png 750w,\n/the-next-website/static/comicanic-01-4225c2494c6188c5c17317b792cc36f5-0f681.png 1500w,\n/the-next-website/static/comicanic-01-4225c2494c6188c5c17317b792cc36f5-606ea.png 2250w,\n/the-next-website/static/comicanic-01-4225c2494c6188c5c17317b792cc36f5-ce3b6.png 3000w,\n/the-next-website/static/comicanic-01-4225c2494c6188c5c17317b792cc36f5-c201b.png 4500w,\n/the-next-website/static/comicanic-01-4225c2494c6188c5c17317b792cc36f5-3db0c.png 5334w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/comicanic/","name":"/Comicanic/"}}

